/**
 * @file TimetableEventCard.js
 * @description A card component that displays a single timetable event with interactive controls.
 * It shows event details, status, and provides actions like edit, check/uncheck, and delete.
 * 
 * @module components/TimetableEventCard
 * @requires react
 * @requires framer-motion
 * @requires ../context/HandlerContext
 */

import React from 'react';
import { useHandlers } from '../context/HandlerContext';
import { motion } from 'framer-motion';

/**
 * @typedef {Object} TimetableEvent
 * @property {string} id - Unique identifier for the event
 * @property {string} subject - The subject of the event
 * @property {string} topic - The topic or title of the event
 * @property {string} date - The date of the event in 'YYYY-MM-DD' format
 * @property {string} time - The time of the event in 'HH:MM' format
 * @property {boolean} [checked] - Whether the event is marked as completed
 * @property {string} [notificationId] - ID for any scheduled notifications
 */

/**
 * @typedef {Object} EventStatus
 * @property {string} text - Display text for the status
 * @property {string} colorClass - Tailwind CSS class for text color
 * @property {string} dotClass - Tailwind CSS class for status dot
 */

/**
 * TimetableEventCard Component
 * 
 * @component
 * @param {Object} props - Component props
 * @param {TimetableEvent} props.event - The event data to display
 * @param {TimetableEvent} [props.nextEvent] - The next event in the timetable (used for status calculation)
 * @param {boolean} [props.isEditMode=false] - Whether the card is in edit mode
 * @param {Function} [props.onEdit] - Callback function when edit button is clicked
 * @param {string} [props.status] - Explicit status override (optional)
 * @returns {React.ReactElement} A card element displaying the timetable event
 * 
 * @example
 * <TimetableEventCard 
 *   event={eventData}
 *   nextEvent={nextEventData}
 *   isEditMode={false}
 *   onEdit={handleEdit}
 * />
 */
const TimetableEventCard = React.memo(({ event, nextEvent, isEditMode = false, onEdit, status }) => {
    const handlers = useHandlers();

    // Removed sortedEvents useMemo as this component now receives a single event.
    // Sorting is handled in TimetablePage.js before passing individual events.

    // Removed formatDateForDisplay as date navigation is no longer in this component.
    // Removed handlePrevDay and handleNextDay as date navigation is no longer in this component.

    /**
     * Calculates the study day for a given date, considering a day to be from 4am to 4am
     * 
     * @private
     * @param {Date|string} dateObj - The date to calculate the study day for
     * @returns {Date} The start of the study day (previous day if before 4am)
     */
    function getStudyDay(dateObj) {
        const studyDay = new Date(dateObj);
        if (studyDay.getHours() < 4) {
            studyDay.setDate(studyDay.getDate() - 1);
        }
        studyDay.setHours(0, 0, 0, 0);
        return studyDay;
    }

    /**
     * Determines the status of an event (Completed, Missed, or Pending).
     * 
     * @private
     * @param {TimetableEvent} event - The event to determine status for
     * @param {TimetableEvent} nextEvent - The next event in the timetable
     * @param {Date} [now=new Date()] - The current date and time for reference
     * @returns {EventStatus} An object with status text and styling classes
     * 
     * @example
     * const status = getEventStatus(event, nextEvent);
     * // Returns: { text: 'Upcoming', colorClass: 'text-blue-600', dotClass: 'bg-blue-500' }
     */
    const getEventStatus = (event, nextEvent, now = new Date()) => {
        if (event.checked) {
            return { text: 'Completed', colorClass: 'text-green-600', dotClass: 'bg-green-500' };
        }
        const eventStart = new Date(`${event.date}T${event.time}`);
        const DEFAULT_DURATION_MINUTES = 180;
        const eventEnd = new Date(eventStart.getTime() + DEFAULT_DURATION_MINUTES * 60000);
        const nextStart = nextEvent ? new Date(`${nextEvent.date}T${nextEvent.time}`) : null;
        const studyDay = getStudyDay(eventStart);
        const studyDayEnd = new Date(studyDay);
        studyDayEnd.setDate(studyDay.getDate() + 1);
        studyDayEnd.setHours(4, 0, 0, 0); // 4am next day
        // If now is after study day end, missed
        if (now > studyDayEnd) {
            return { text: 'Missed', colorClass: 'text-red-600', dotClass: 'bg-red-500' };
        }
        // If now is before event start time
        if (now < eventStart) {
            return { text: 'Upcoming', colorClass: 'text-orange-600', dotClass: 'bg-orange-400' };
        }
        // If now is between event start and event end: Live
        if (now >= eventStart && now < eventEnd) {
            return { text: 'Live', colorClass: 'text-cyan-600', dotClass: 'bg-cyan-500' };
        }
        // If now is after event end and before next event's start (or no next event): Missed
        if (now >= eventEnd && (!nextStart || now < nextStart)) {
            return { text: 'Missed', colorClass: 'text-red-600', dotClass: 'bg-red-500' };
        }
        // If now is after next event's start: Missed
        if (nextStart && now >= nextStart) {
            return { text: 'Missed', colorClass: 'text-red-600', dotClass: 'bg-red-500' };
        }
        // Default fallback
        return { text: 'Upcoming', colorClass: 'text-orange-600', dotClass: 'bg-orange-400' };


    };

    // Determine status object based on props or calculate it
    /** @type {EventStatus} */
    let statusObj;
    
    // If status prop is provided, use it for text/color, otherwise fallback to local calculation
    if (status) {
        switch (status) {
            case 'Completed':
                statusObj = { text: 'Completed', colorClass: 'text-green-600', dotClass: 'bg-green-500' };
                break;
            case 'Live':
                statusObj = { text: 'Live', colorClass: 'text-cyan-600', dotClass: 'bg-cyan-500' };
                break;
            case 'Missed':
                statusObj = { text: 'Missed', colorClass: 'text-red-600', dotClass: 'bg-red-500' };
                break;
            case 'Pending':
                statusObj = { text: 'Upcoming', colorClass: 'text-orange-600', dotClass: 'bg-orange-400' };
                break;
            default:
                statusObj = { text: status, colorClass: 'text-gray-600', dotClass: 'bg-gray-300' };
        }
    } else {
        // Calculate status based on event times
        statusObj = getEventStatus(event, nextEvent, new Date());
    }

    // Card background: green for completed, gray for normal, red for missed
    let cardClasses = `flex justify-between items-center p-4 mb-3 rounded-xl`;
    if (event.checked || (status && status === 'Completed')) {
        cardClasses += ' bg-green-100'; // Completed: green background
    } else if ((status && status === 'Missed') || (!status && statusObj.text === 'Missed')) {
        cardClasses += ' bg-red-100'; // Missed: red background
    } else {
        cardClasses += ' bg-gray-50'; // Normal: subtle gray background
    }

    return (
        <div
            id={`timetable-event-${event.id}`}
            className={cardClasses}
        >
            <div>
                <p className="text-sm text-gray-500">{event.time}</p>
                <p className={`text-lg font-semibold text-gray-800 ${event.checked ? 'line-through opacity-70' : ''}`}>{event.subject}</p>
                <h4 className="text-base text-gray-600">{event.topic}</h4>
                {/* Status with colored dot (always for Live, Pending, Missed; never for Completed) */}
                {!event.checked && statusObj && (
                  <span className="flex items-center gap-1 mt-1">
                    <span className={`inline-block w-3 h-3 rounded-full border border-white flex-shrink-0 mr-2 ${statusObj.dotClass}`}></span>
                    <span className={`text-sm font-semibold ${statusObj.colorClass}`}>{statusObj.text}</span>
                  </span>
                )}
            </div>
            <div className="flex items-center gap-1">
                {isEditMode ? (
                    <>
                        {/* Edit button, visible only in edit mode */}
                        <button
                            onClick={() => onEdit(event.id)}
                            className="p-1.5 mr-4 rounded-full text-gray-400 hover:text-blue-500 transition-colors duration-200"
                            aria-label="Edit event"
                        >
                            <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path>
                                <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path>
                            </svg>
                        </button>
                        {/* Delete button, visible only in edit mode */}
                        <button
                            onClick={() => handlers.deleteTimetableEntry(event.id)}
                            className="p-1.5 rounded-full text-gray-400 hover:text-red-500 transition-colors duration-200 mr-2"
                            aria-label="Delete event"
                        >
                            <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round">
                                <path d="M3 6h18"></path>
                                <path d="M19 6v14c0 1-1 2-2 2H7c-1 0-2-1-2-2V6"></path>
                                <path d="M8 6V4c0-1 1-2 2-2h4c1 0 2 1 2 2v2"></path>
                                <line x1="10" y1="11" x2="10" y2="17"></line>
                                <line x1="14" y1="11" x2="14" y2="17"></line>
                            </svg>
                        </button>
                    </>
                ) : (
                    // Check/uncheck button for marking completion, visible only NOT in edit mode
                    <motion.button
                        onClick={() => handlers.checkTimetableEntry(event.id)}
                        className={`w-6 h-6 rounded-full border-2 flex items-center justify-center`}
                        aria-label={event.checked ? "Mark as incomplete" : "Mark as complete"}
                        whileTap={{ scale: 0.85 }}
                        animate={{
                            scale: event.checked ? 1.1 : 1,
                            backgroundColor: event.checked ? '#22c55e' : '#ffffff00',
                            borderColor: event.checked ? '#22c55e' : '#d1d5db'
                        }}
                        transition={{ type: 'spring', stiffness: 400, damping: 15 }}
                    >
                        {/* Checkmark icon if event is checked */}
                        {event.checked && (
                            <motion.svg
                                initial={{ scale: 0, rotate: -90 }}
                                animate={{ scale: 1, rotate: 0 }}
                                transition={{ type: 'spring', stiffness: 500, damping: 20 }}
                                xmlns="http://www.w3.org/2000/svg"
                                className="h-4 w-4 text-white"
                                fill="none"
                                viewBox="0 0 24 24"
                                stroke="currentColor"
                            >
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={3} d="M5 13l4 4L19 7" />
                            </motion.svg>
                        )}
                    </motion.button>
                )}
            </div>
        </div>
    );
});

export default TimetableEventCard;