// src/pages/TimetablePage.js
import React, { useState, useMemo, useCallback } from 'react';
import TimetableEventCard from '../components/TimetableEventCard';
import { useHandlers } from '../context/HandlerContext';
import EditTimetableModal from '../components/EditTimetableModal';
import AddModal from '../components/AddModal';
import EmptyState from '../components/EmptyState';


/**
 * Helper function to get a 'YYYY-MM-DD' formatted string from a Date object,
 * based on the user's local time zone. This prevents UTC conversion issues.
 * @param {Date} date - The date object to format.
 * @returns {string} The formatted date string.
 */
const getLocalDateString = (date) => {
    const year = date.getFullYear();
    const month = (date.getMonth() + 1).toString().padStart(2, '0');
    const day = date.getDate().toString().padStart(2, '0');
    return `${year}-${month}-${day}`;
};

// Custom areEqual function for React.memo
function areEqual(prevProps, nextProps) {
    if (
        prevProps.timetableEntries.length !== nextProps.timetableEntries.length ||
        prevProps.selectedDisplayDate !== nextProps.selectedDisplayDate
    ) {
        return false;
    }
    // Compare each event by id and checked (add more fields if needed)
    for (let i = 0; i < prevProps.timetableEntries.length; i++) {
        const prevEvent = prevProps.timetableEntries[i];
        const nextEvent = nextProps.timetableEntries[i];
        if (
            prevEvent.id !== nextEvent.id ||
            prevEvent.checked !== nextEvent.checked ||
            prevEvent.subject !== nextEvent.subject ||
            prevEvent.topic !== nextEvent.topic ||
            prevEvent.date !== nextEvent.date ||
            prevEvent.time !== nextEvent.time
        ) {
            return false;
        }
    }
    return true;
}

// Memoize TimetablePage to prevent unnecessary re-renders
const TimetablePage = React.memo(({ timetableEntries, showNotification, userId }) => {
    const handlers = useHandlers();
    const [timetableEditMode, setTimetableEditMode] = useState(false);
    const [isAddModalOpen, setIsAddModalOpen] = useState(false);
    const [isEditModalOpen, setIsEditModalFromState] = useState(false); // Renamed to avoid confusion
    const [currentEventToEdit, setCurrentEventToEdit] = useState(null);

    const [selectedDisplayDate, setSelectedDisplayDate] = useState(getLocalDateString(new Date()));
    // Search state with validation
    const [searchQuery, setSearchQuery] = useState("");
    const [isSearching, setIsSearching] = useState(false);
    const [searchError, setSearchError] = useState("");

    // Helper to control modal state consistently
    const setIsEditModalOpen = useCallback((isOpen) => {
        setIsEditModalFromState(isOpen);
        if (!isOpen) {
            setCurrentEventToEdit(null); // Clear event data when modal closes
        }
    }, []);

    // Memoized handlers for toggles and modals
    const handleToggleEditMode = useCallback(() => {
        setTimetableEditMode(prev => !prev);
    }, []);

    const handleCloseAddModal = useCallback(() => {
        setIsAddModalOpen(false);
    }, []);

    const handleCloseEditModal = useCallback(() => {
        setIsEditModalOpen(false);
    }, [setIsEditModalOpen]);

    // (Moved above usage)
    const handleSaveEvent = (data) => {
        handlers.addTimetableEntry(data.subject, data.topic, data.date, data.time);
    };

    const handleEditEvent = useCallback((eventId) => {
        const eventToEdit = timetableEntries.find(event => event.id === eventId);
        if (eventToEdit) {
            setCurrentEventToEdit(eventToEdit);
            setIsEditModalOpen(true);
        }
    }, [timetableEntries, setIsEditModalOpen]);

    const handleDeleteTimetableEntry = useCallback((id) => {
        handlers.deleteTimetableEntry(id);
    }, [handlers]);

    const handleCheckTimetableEntry = useCallback((id) => {
        handlers.checkTimetableEntry(id);
    }, [handlers]);

    // This handleUpdateEvent now correctly calls the updateTimetableEntry prop from App.js
    // which already has userId and showNotification in its closure.
    const handleUpdateEvent = useCallback(async (eventId, subject, topic, date, time) => {
        await handlers.updateTimetableEntry(eventId, subject, topic, date, time);
        setIsEditModalOpen(false); // Close modal using the consistent helper
    }, [handlers, setIsEditModalOpen]);

    const handleAddSimilarEvent = useCallback((eventData) => {
        handlers.addTimetableEntry(
            eventData.subject,
            eventData.topic,
            eventData.date,
            eventData.time
        );
    }, [handlers]);

    const memoizedSelectedDateObject = useMemo(() => {
        const date = new Date(selectedDisplayDate);
        const userTimezoneOffset = date.getTimezoneOffset() * 60000;
        return new Date(date.getTime() + userTimezoneOffset);
    }, [selectedDisplayDate]);

    // Chronologically sort all entries by date, then by time
    const sortedTimetableEntries = useMemo(() => {
        return [...timetableEntries].sort((a, b) => {
            if (a.date !== b.date) return a.date.localeCompare(b.date);
            return a.time.localeCompare(b.time);
        });
    }, [timetableEntries]);

    // Filtered and sorted entries based on search (searches across all entries)
    const filteredEntries = useMemo(() => {
        if (!searchQuery.trim() || searchQuery.trim().length < 2) {
            // If not searching or search query is too short, show only today's entries, sorted
            return sortedTimetableEntries.filter(entry => entry.date === selectedDisplayDate);
        }
        const q = searchQuery.toLowerCase();
        return sortedTimetableEntries.filter(entry =>
            entry.subject.toLowerCase().includes(q) ||
            entry.topic.toLowerCase().includes(q) ||
            entry.time.toLowerCase().includes(q)
        );
    }, [sortedTimetableEntries, searchQuery, selectedDisplayDate]);

    // Utility: calculate event end time (default 1 hour duration if not specified)
    const getEventEndTime = (event) => {
        if (event.endTime) {
            return new Date(`${event.date}T${event.endTime}`);
        }
        const [hour] = event.time.split(':').map(Number);
        return new Date(new Date(`${event.date}T${event.time}`).setHours(hour + 1));
    };

    // Calculate status for each event
    const filteredEntriesWithStatus = useMemo(() => {
        const now = new Date();
        return filteredEntries.map((event) => {
            if (event.checked) return { ...event, status: 'Completed' };
            // Find the true next event in the full sorted timetable
            const idxInFull = sortedTimetableEntries.findIndex(e => e.id === event.id);
            const nextEvent = idxInFull !== -1 ? sortedTimetableEntries[idxInFull + 1] : undefined;
            const eventStart = new Date(`${event.date}T${event.time}`);
            const eventEnd = getEventEndTime(event);
            const nextStart = nextEvent ? new Date(`${nextEvent.date}T${nextEvent.time}`) : null;
            if (now >= eventStart && (!nextStart || now < nextStart)) {
                return { ...event, status: 'Live' };
            } else if (now < eventStart) {
                return { ...event, status: 'Pending' };
            } else if (now > eventEnd && nextEvent && now > nextStart && !event.checked) {
                return { ...event, status: 'Missed' };
            }
            return { ...event, status: 'Pending' };
        });
    }, [filteredEntries, sortedTimetableEntries]);

    // Group filtered entries by date for search results
    const groupedByDate = useMemo(() => {
        if (!searchQuery.trim()) return {};
        return filteredEntriesWithStatus.reduce((acc, entry) => {
            if (!acc[entry.date]) acc[entry.date] = [];
            acc[entry.date].push(entry);
            return acc;
        }, {});
    }, [filteredEntriesWithStatus, searchQuery]);

    const addModalFields = useMemo(() => [
        { name: 'subject', label: 'Subject', placeholder: 'e.g., Anatomy', required: true },
        { name: 'topic', label: 'Topic', placeholder: 'e.g., Cardiovascular System', required: true },
        { name: 'date', label: 'Date', type: 'date', required: true, defaultValue: selectedDisplayDate },
        { name: 'time', label: 'Time', type: 'time', required: true, defaultValue: '09:00' }
    ], [selectedDisplayDate]);

    const handleSelectCalendarDate = useCallback((date) => {
        setSelectedDisplayDate(getLocalDateString(date));
    }, []);

    const handlePrevDay = useCallback(() => {
        const newDate = new Date(memoizedSelectedDateObject);
        newDate.setDate(newDate.getDate() - 1);
        handleSelectCalendarDate(newDate);
    }, [memoizedSelectedDateObject, handleSelectCalendarDate]);

    const handleNextDay = useCallback(() => {
        const newDate = new Date(memoizedSelectedDateObject);
        newDate.setDate(newDate.getDate() + 1);
        handleSelectCalendarDate(newDate);
    }, [memoizedSelectedDateObject, handleSelectCalendarDate]);

    return (
        <>
            <div id="page-timetable" className="page px-6 py-8">
                <header className="flex items-center justify-between mb-2">
                    <h1 className="text-3xl font-bold text-gray-900">Timetable</h1>
                        <button
                        onClick={handleToggleEditMode}
                        className="bg-blue-50 text-blue-700 font-semibold text-xs px-4 py-2 rounded-full flex items-center hover:bg-blue-100 transition duration-200"
                        aria-label={timetableEditMode ? 'Done editing timetable' : 'Edit timetable'}
                        >
                        <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="16"
                        height="16"
                        viewBox="0 0 24 24"
                        fill="none"
                        stroke="currentColor"
                        strokeWidth="2"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        className="mr-1"
                        >
                        <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path>
                        <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path>
                        </svg>
                        {timetableEditMode ? 'Done' : 'Edit'}
                        </button>
                </header>
                <p className="text-gray-500 mb-6">Upcoming Study Schedule</p>

                {/* Enhanced Search Bar */}
                <div className="mb-6 relative">
                    <div className="relative">
                        <input
                            type="text"
                            value={searchQuery}
                            onChange={(e) => {
                                const value = e.target.value;
                                setSearchQuery(value);
                                
                                // Clear any previous errors
                                if (searchError) setSearchError('');
                                
                                // Show loading state if query is long enough
                                if (value.trim().length >= 2) {
                                    setIsSearching(true);
                                    // Simulate search delay
                                    const timer = setTimeout(() => {
                                        setIsSearching(false);
                                    }, 300);
                                    return () => clearTimeout(timer);
                                } else if (value.trim().length > 0) {
                                    setSearchError('Please enter at least 2 characters');
                                }
                            }}
                            placeholder="Search events by subject, topic, or time..."
                            className={`w-full pl-10 pr-10 py-2 rounded-lg border ${
                                searchError ? 'border-red-400' : 'border-gray-300'
                            } focus:outline-none focus:ring-2 focus:ring-blue-500 bg-white transition-colors duration-200`}
                            aria-label="Search timetable events"
                            aria-invalid={!!searchError}
                            aria-describedby={searchError ? 'search-error' : undefined}
                        />
                        <div className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400">
                            {isSearching ? (
                                <svg className="animate-spin h-4 w-4 text-blue-500" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                                </svg>
                            ) : (
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                                </svg>
                            )}
                        </div>
                        {searchQuery && (
                            <button
                                onClick={() => {
                                    setSearchQuery('');
                                    setSearchError('');
                                }}
                                className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600 focus:outline-none"
                                aria-label="Clear search"
                            >
                                <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                                </svg>
                            </button>
                        )}
                    </div>
                    <div className="flex justify-between mt-1 px-1">
                        {searchError ? (
                            <p id="search-error" className="text-xs text-red-500">{searchError}</p>
                        ) : searchQuery ? (
                            <p className="text-xs text-gray-500">
                                {searchQuery.length}/50 characters  {filteredEntries.length} event{filteredEntries.length !== 1 ? 's' : ''} found
                            </p>
                        ) : (
                            <p className="text-xs text-gray-400">Type at least 2 characters to search events</p>
                        )}
                    </div>
                </div>

                {/* Only show Add New button and date picker if not searching */}
                {(!searchQuery.trim()) && (
                    <>
                <button
                    onClick={() => setIsAddModalOpen(true)}
                    className="w-full bg-blue-50 text-blue-700 font-semibold mt-6 py-2 px-4 rounded-lg flex items-center justify-center hover:bg-blue-100 transition duration-300 mb-2"
                >
                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" fill="none" stroke="currentColor" strokeWidth="2" className="mr-2"><line x1="12" y1="5" x2="12" y2="19"></line><line x1="5" y1="12" x2="19" y2="12"></line></svg>
                    Add New Study Event
                </button>

                <div className="flex items-center justify-between px-0 py-6 text-gray-700 font-semibold text-lg">
                    <button onClick={handlePrevDay} className="p-2 rounded-full hover:bg-gray-100 transition-colors duration-200" aria-label="Previous day">
                        <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><polyline points="15 18 9 12 15 6"></polyline></svg>
                    </button>
                    <span className="flex-grow text-center text-lg sm:text-base">
                        {memoizedSelectedDateObject.toLocaleDateString('en-US', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' })}
                    </span>
                    <button onClick={handleNextDay} className="p-2 rounded-full hover:bg-gray-100 transition-colors duration-200" aria-label="Next day">
                        <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"><polyline points="9 18 15 12 9 6"></polyline></svg>
                    </button>
                </div>
                    </>
                )}

                <div id="timetable-events-list" className="space-y-4 bg-white rounded-lg min-h-[100px]">
                    {filteredEntriesWithStatus.length === 0 ? (
                        <EmptyState
                            title={searchQuery.trim().length >= 2 ? 'No events found' : 'Your timetable is empty'}
                            description={
                                searchQuery.trim().length >= 2
                                    ? 'No events match your search. Try different keywords or check your spelling.'
                                    : 'Add your first event to get started!'
                            }
                            icon={
                                <svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="1.5" strokeLinecap="round" strokeLinejoin="round" className="text-gray-300">
                                    <rect x="3" y="4" width="18" height="18" rx="2" ry="2"></rect>
                                    <line x1="16" y1="2" x2="16" y2="6"></line>
                                    <line x1="8" y1="2" x2="8" y2="6"></line>
                                    <line x1="3" y1="10" x2="21" y2="10"></line>
                                    <path d="M8 14h.01"></path>
                                    <path d="M12 14h.01"></path>
                                    <path d="M16 14h.01"></path>
                                    <path d="M8 18h.01"></path>
                                    <path d="M12 18h.01"></path>
                                    <path d="M16 18h.01"></path>
                                </svg>
                            }
                            action={
                                <button
                                    onClick={() => setIsAddModalOpen(true)}
                                    className="mt-4 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition-colors duration-200"
                                >
                                    <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 mr-2" viewBox="0 0 20 20" fill="currentColor">
                                        <path fillRule="evenodd" d="M10 5a1 1 0 011 1v3h3a1 1 0 110 2h-3v3a1 1 0 11-2 0v-3H6a1 1 0 110-2h3V6a1 1 0 011-1z" clipRule="evenodd" />
                                    </svg>
                                    Add Your First Event
                                </button>
                            }
                        />
                    ) : (
                        <div className="space-y-4">
                            {/* If searching, group by date and show bold date headers */}
                            {searchQuery.trim() ? (
                                Object.keys(groupedByDate).sort().map(date => (
                                    <div key={date}>
                                        <div className="font-bold text-lg text-blue-800 mb-2 mt-6 pl-1 tracking-wide">
                                            {new Date(date).toLocaleDateString('en-US', { weekday: 'short', year: 'numeric', month: 'short', day: 'numeric' })}
                                        </div>
                                        <div className="space-y-4">
                                            {groupedByDate[date].map((event, i, arr) => {
                                                const statusEvent = filteredEntriesWithStatus.find(e => e.id === event.id) || event;
                                                return (
                                                    <TimetableEventCard
                                                        key={event.id}
                                                        event={event}
                                                        status={statusEvent.status}
                                                        nextEvent={arr[i+1]}
                                                        isEditMode={timetableEditMode}
                                                        onDelete={handlers.deleteTimetableEntry}
                                                        onCheck={handlers.checkTimetableEntry}
                                                        onEdit={handleEditEvent}
                                                    />
                                                );
                                            })}
                                        </div>
                                    </div>
                                ))
                            ) : (
                                filteredEntriesWithStatus.map((event, idx) => (
                                    <TimetableEventCard
                                        key={event.id}
                                        event={event}
                                        status={event.status}
                                        nextEvent={filteredEntriesWithStatus[idx+1]}
                                        isEditMode={timetableEditMode}
                                        onDelete={handleDeleteTimetableEntry}
                                        onCheck={handleCheckTimetableEntry}
                                        onEdit={handleEditEvent}
                                    />
                                ))
                            )}
                        </div>
                    )}
                </div>
            </div>

            <AddModal
                isOpen={isAddModalOpen}
                onClose={handleCloseAddModal}
                onSave={handleSaveEvent}
                title="Add New Study Event"
                fields={addModalFields}
            />

        <EditTimetableModal
            isOpen={isEditModalOpen}
            onClose={handleCloseEditModal}
            eventData={currentEventToEdit}
            onUpdate={handleUpdateEvent}
            onAddSimilar={handleAddSimilarEvent}
            showNotification={showNotification}
        />
    </>
);
}, areEqual);

export default TimetablePage;