import React from 'react';
import PropTypes from 'prop-types';
import { motion } from 'framer-motion';

/**
 * EmptyState Component
 * 
 * A reusable component to display a friendly message when there's no content to show.
 * 
 * @param {Object} props - Component props
 * @param {string} [props.title='No items found'] - The main title/message
 * @param {string} [props.description='There are no items to display at the moment.'] - Additional description text
 * @param {React.ReactNode} [props.icon] - Optional icon to display
 * @param {React.ReactNode} [props.action] - Optional call-to-action button or element
 * @param {string} [props.className=''] - Additional CSS classes
 * @returns {React.ReactElement} The EmptyState component
 */
const EmptyState = ({
  title = 'No items found',
  description = 'There are no items to display at the moment.',
  icon,
  action,
  className = ''
}) => {
  return (
    <motion.div 
      className={`flex flex-col items-center justify-center p-8 text-center ${className}`}
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.3 }}
    >
      {icon && <div className="mb-4 text-gray-400">{icon}</div>}
      <h3 className="text-lg font-medium text-gray-900 mb-2">{title}</h3>
      <p className="text-gray-500 mb-6 max-w-md">{description}</p>
      {action && <div className="mt-2">{action}</div>}
    </motion.div>
  );
};

EmptyState.propTypes = {
  title: PropTypes.string,
  description: PropTypes.string,
  icon: PropTypes.node,
  action: PropTypes.node,
  className: PropTypes.string,
};

export default EmptyState;
